
function getRandomArrayElement(items) {
  return items[Math.floor(Math.random() * items.length)];
}

/**
 *    : taxi - Taxi
 * @param {string} str 
 */
const typeNameNormalize = (str) => str.charAt(0).toUpperCase() + str.slice(1);
/**
 *    
 * @param {string} key      
 * @param {Array} arr  
 */
const getAllKeyValue = (key,arr) => arr.map((item) => item[key]);
/**
 *  item     id
 * @param {Number} id id     item
 * @param {Array} arr  
 */
const getItemById = (id,arr) => arr.find((item) => item.id === id);

//    
function updateItem(items, update) {
  return items.map((item) => item.id === update.id ? update : item);
}

const getOffersByType = (offers,type) =>{
  const offer = offers.filter((item) =>item.type === type);
  if(offer.length <= 0){
    return [];
  }
  return offer[0].offers;
};

export { getRandomArrayElement, typeNameNormalize, getAllKeyValue, getItemById, updateItem, getOffersByType };
